.movies-card__container {
  display: flex;
  justify-content: space-between;
  padding: 14px 0px 29px;
  align-items: center;
  border-bottom: 1px solid #E8E8E8;
}

.movies-card__title {
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  color: #000000;
  margin: 0;
}

.movies-card__button {
  background: no-repeat center;
  width: 24px;
  height: 24px;
  border: none;
  padding: 0;
  cursor: pointer;
}

.movies-card__button_like-disable {
  background: url(/static/media/btn__like_disabled.6b4998ff.svg);
}

.movies-card__button_like-active {
  background-image: url(/static/media/btn__like_active.d0e70db1.svg);
}

.movies-card__button_delete {
  background-image: url(/static/media/btn__delete.d2e007b4.svg);
}

.movies-card__duration {
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  display: flex;
  align-items: center;
  color: #A0A0A0;
  margin: 6px 0px 10px;
}


.saved-movies__page {
  background-color: #FAFAFA;
  margin: auto;
  padding: 0px 70px;
}

@media screen and (max-width: 800px) {
  .saved-movies__page {
    padding: 0px 30px;
  }
}

@media screen and (max-width: 400px) {
  .saved-movies__page {
    padding: 0px 10px;
  }
}

.movies-card-list {
  text-align: center;
}

.movies-card-list__elements {
  display: grid;
  grid-gap: 30px 20px;
  gap: 30px 20px;
  grid-template-columns: repeat(auto-fit, minmax(270px,.7fr));
  grid-template-rows: auto;
  justify-content: center;
  max-width: 1140px;
  margin: 70px auto auto;
}

.movies-card-list__more-btn {
  border: 1px solid #E8E8E8;
  background-color: transparent;
  border-radius: 2px;
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  margin: 50px auto 80px;
  padding: 0;
  transition: .3s;
  width: 320px;
  height: 36px;
}

.movies-card-list__more-btn_hidden {
  visibility: hidden;
}

.movies-card-list__more-btn:hover {
  opacity: .8;
}

.movies-card__img {
  object-fit: cover;
  width: 100%;
}

