.profile {
  margin: auto;
  padding: 0px 70px;

}

.profile__content {
  max-width: 410px;
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 74px 0px 70px;
  text-align: center;
}


.profile__title{
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  margin-bottom: 110px;
}

.profile__container-name {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #E8E8E8;
}

.profile__container-email {
  display: flex;
  justify-content: space-between;
  margin-bottom: 210px;
}

.profile__input {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
  width: 100%;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  text-align: right;
}

.profile__name {
  white-space: nowrap;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;
}

.profile__button {
  cursor: pointer;
  transition: .3s;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  border: none;
  background-color: transparent;
  text-decoration: none;
}

.profile__button:hover {
  opacity: .8;
}

.profile__button_signout {
  margin-top: 16px;
  font-weight: 500;
  color: #EE3465;
}

@media screen and (max-width: 800px) {
  .profile {
    padding: 0px 30px;
  }
}

.register {
  margin: 70px auto;
  padding: 0px 70px;
  max-width: 400px;
  width: 100%;
}

.register__header-title {
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin: 40px auto;
}

.register__fieldset {
  padding: 0;
  border: none;
  position:relative
}

.register__form-title {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #A0A0A0;
}

.register__form-input {
  background: #F4F4F4;
  border-radius: 8px;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #000000;
  height: 46px;
  padding: 0px 15px 0px;
  border: none;
  outline: none;
  margin-bottom: 10px;
  width: 100%;
