}

.register__input-error {
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: #EE3465;
  position: absolute;
  margin-top: 50px;
  margin-left: -396px;
}

.register__button {
  transition: .3s;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #FFFFFF;
  background: #3456F3;
  border-radius: 3px;
  border: none;
  height: 45px;
  margin: 70px 0px 16px;
  width: 100%;
}

.register__button {
  opacity: .8;
}

.register__login-container {
  text-align: center;
}

.register__login-message {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #A0A0A0;
  margin-right: 6px;
}

.register__login-button {
  transition: .3s;
  cursor: pointer;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #3456F3;
  border: none;
  background: transparent;
  text-decoration: none;
}

.register__login-button:hover {
  opacity: .8;
}

